.intro {
  position: relative;
  height: 100vh;
  width: 100%;
  background-image: url("../img/bg1.png");
  background-position: 0 0;
  &__container {
    width: 700px;
    display: block;
    position: relative;
    top: 300px;
    left: 300px;
    & h1,p {
      text-align: center; }
    & h1 {
      font-size: 30px; }
    & p {
      font-size: 30px; }
    &__btn {
      text-align: center;
      background-color: greenyellow;
      border-radius: 30px; } } }





